import React, { Component } from 'react';
import firebase from '../common/firebase';

class SignUp extends Component {
    
    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <h1>SignUp</h1>
                        <hr></hr>
                        <SignUpForm />
                    </div>
                </div>
                
            </div>
        );
    }
}

class SignUpForm extends Component {
    state = {
        email: '',
        password: '',
        error: null,
    };
    
    handleSubmit = (event) => {
        event.preventDefault();
        const { email, password } = this.state;
        
        console.log(email);
        console.log(password);

        firebase.auth().createUserWithEmailAndPassword(email, password)
            .then((user) => {
                alert("here")
                console.log(user);

                this.props.history.push('/');
            })
            .catch((error) => {
                this.setState({ error: error });
                alert("error here" + error);

            });
    };

    onSubmitForm = (e) => {
        e.preventDefault();
        
        const { email, password } = this.state;

        console.log(email);
        console.log(password);

        firebase.auth().createUserWithEmailAndPassword(email, password)
            .then((user) => {
                alert("here")
                this.props.history.push('/');
            })
            .catch((error) => {
                this.setState({ error: error });
                alert("error here" + error);

            });
    };

    input;

    onRefInput = (c) => {
        this.input = c;
    };

    onChangeInputEmail  = (e) => {
        this.setState({
            email : e.target.value
        });
    };

    onChangeInputPassword  = (e) => {
        this.setState({
            password : e.target.value
        });
    };

    render() {
        
        return (
            <>
                <form onSubmit={ this.onSubmitForm }>
                    <div className="form-group">
                        <label>Email</label>
                        <input type="text" className="form-control" id="inputAddress" placeholder="Email" ref={ this.onRefInput } value={ this.state.email } onChange={ this.onChangeInputEmail } />
                    </div>

                    <div className="form-group">
                        <label>Password</label>
                        <input type="text" className="form-control" id="inputAddress" placeholder="Password" ref={ this.onRefInput } value={ this.state.password } onChange={this.onChangeInputPassword} />
                    </div>

                    <button type="submit" className="btn btn-primary">Sign in</button>
                </form>
            </>
        );
    }
}

export default SignUp;
